/** @format */

import React, { Component } from "react";
import { Redirect } from "react-router-dom";

class Cart extends Component {
  render() {
    console.log(this.props.isAuth)
    if (this.props.isAuth == false) {
      return <Redirect to="/auth"></Redirect>;
    } else {
      return (
        <div className="">
          {this.props.cartItem.length != 0 ? (
            <div className="container " style={{height: "76vh"}}>
              <div className="card-header bg-light text-center display-3">
                Items In Cart
              </div>

              <div className="row justify-content-center d-flex flex-column align-items-center">
                {this.props.cartItem.map((ele, id) => {
                  return (
                    <div
                      key={id}
                      className="col-sm-10 col-md-8 col-lg-6 bg-light d-flex align-items-center justify-content-between"
                    >
                      <span className="p-3 ">{ele.name}</span>
                      <span className="p-3 ">Rs {ele.price}</span>
                      <span className="float-right">
                        <img
                          src={ele.avatar}
                          alt="d"
                          style={{ height: "100px", width: "100px" }}
                        ></img>
                      </span>
                    </div>
                  );
                })}
              </div>
            </div>
          ) : (
            <div className="row">
              <div className="col text-center display-2 text-danger" style={{height: "76vh"}}>
                No Item Added!!!
              </div>
            </div>
          )}
        </div>
      );
    }
  }
}

export default Cart;
