/** @format */

import React, { Component } from "react";
import { Link } from "react-router-dom";
import swal from "sweetalert";

class Product extends Component {
  constructor(props) {
    super(props);
    this.state = {
     
    };
  }
 
  checkAuth = () => {
    console.log(this.props.isAuth)
     if(this.props.isAuth === false) {
      this.props.history.push("/auth");
    }else{
      const productObj = this.props.data.find(
        element => element.id === this.props.match.params.id
      );
      this.props.updateCart({ ...productObj });
      swal(
        "Item Added To Cart!!",
        "Successfully added",
        "success"
      );
      
    }
    
  };


  render() {
    const productObj = this.props.data.find(
      element => element.id == this.props.match.params.id
    );
    return productObj !== undefined ? (
      <div className="row justify-content-center" >
        <div className="card mb-3" >
          <div className="card-body " >
            <img src={productObj.avatar} alt="ds" className="img-fluid" />
          </div>
          <div className="card-footer text-center text-primary h2">
            <p> Name : {productObj.name} </p>
            <p> Description :{productObj.description}</p>
            <p> Price : {productObj.price} </p>
            <Link to="/iphone" className="btn btn-primary">
              Go Back
            </Link>
            <button
              type="button"
              className="btn btn-primary mx-2"
              onClick={this.checkAuth}
            >
              Add to cart
            </button>
           
          </div>
        </div>
      </div>
    ) : (
      <React.Fragment>
        <h1> Product not found, invalid id </h1>
      </React.Fragment>
    );
  }
}
export default Product;
