/** @format */

import React, { Component } from "react";

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: ""
    };
  }
  handleChange = e => {
    const { name, value } = e.target;
    this.setState({
      [name]: value
    });
  };
  submitData = e => {
    e.preventDefault();
    let user = this.state.username;
    let pass = this.state.password;
    this.props.auth(user, pass);
  };

  render() {
    if (this.props.isAuth == true) {
      this.props.history.goBack();
    }
    return (
      <div style={{ padding: 50 }} className="container" style={{height: "76vh"}}>
        <div className="row justify-content-center ">
          <div className="col-12 col-sm-12 col-md-8 col-lg-6 ">
            <h1 className="display-lg-4 text-center text-danger">
              Login
            </h1>
            <br />
            <form onSubmit={this.submitData} className=" h1 ">
              <div
                className="bg-dark  p-3 text-light"
                style={{ borderRadius: "10px" }}
              >
                <div className="form-group">
                  <label htmlFor="userN">Username</label>
                  <input
                    id="userN"
                    name="username"
                    className="form-control"
                    placeholder="enter username- admin"
                    onChange={this.handleChange}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="pass">Password</label>
                  <input
                    id="pass"
                    name="password"
                    className="form-control"
                    placeholder="enter password- admin"
                    onChange={this.handleChange}
                  />
                </div>
                <div className="row justify-content-center">
                  <button
                    type="submit"
                    className="btn-lg btn-danger float-right"
                  >
                    Sign In
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

export default Login;
