/** @format */
import axios from "axios";
import Home from "./components/home";
import Faq from "./components/faq";
import Mac from "./components/mac";
import Iphone from "./components/iphone";
import IphoneDesc from "./components/iphoneDesc";
import MacDesc from "./components/macDesc";
import AccessoriesDesc from "./components/accessoriesDesc";
import Accessories from "./components/accessories";
import About from "./components/aboutus";
import Career from "./components/career";
import Cart from "./components/cart";
import Login from "./components/login";
import Navbar from "./components/navbar";
import Footer from "./components/footer";
import Notfound from "./components/notFound.js";
import React, { Component } from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch
} from "react-router-dom";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {},
      isAuth: false,
      loading: false,
      userN: "admin",
      pass: "admin",
      cartItems: []
    };
  }

  componentDidMount() {
    axios
      .get("./myData.json")
      .then(res => {
        const data = res.data;
        this.setState({
          data: data,
          loading: true
        });
      })
      .then(
        err => console.log(err),
        err => {
          if (err !== undefined) {
            this.setState({
              loading: false
            });
          }
        }
      );
  }
  changeAuth = () => {
    this.setState({
      isAuth: false,
      cartItems: []
    });
  };
  checkAuth = (u, p) => {
    const { userN, pass } = this.state;
    if (u == userN && p == pass) {
      this.setState({
        isAuth: true
      });
    }
  };
  updateCart = item => {
    const { cartItems } = this.state;
    this.setState({
      cartItems: [...cartItems, item]
    });
  };

  render() {
    console.log(this.state);
    return (
      <Router>
        <div >
        <Navbar isAuth={this.state.isAuth} changeAuth={this.changeAuth} />
         
        <div className="row">
          <div className="col">
            {this.state.loading ? (
              <Switch>
                <Route path="/" exact component={Home}></Route>
                <Route
                  path="/iphone"
                  exact
                  render={props => (
                    <Iphone data={this.state.data} {...props}></Iphone>
                  )}
                ></Route>
                <Route
                  path="/mac"
                  exact
                  render={props => (
                    <Mac data={this.state.data} {...props}></Mac>
                  )}
                ></Route>
                <Route
                  path="/accessories"
                  exact
                  render={props => (
                    <Accessories
                      data={this.state.data}
                      isAuth={this.state.isAuth}
                      updateCart={this.updateCart}
                      {...props}
                    ></Accessories>
                  )}
                ></Route>
                <Route
                  path="/iphone/:id"
                  render={props => (
                    <IphoneDesc
                      data={this.state.data.iphone}
                      updateCart={this.updateCart}
                      isAuth={this.state.isAuth}
                      {...props}
                    ></IphoneDesc>
                  )}
                ></Route>
                <Route
                  path="/mac/:id"
                  render={props => (
                    <MacDesc
                      data={this.state.data.mac}
                      updateCart={this.updateCart}
                      {...props}
                      isAuth={this.state.isAuth}
                    ></MacDesc>
                  )}
                ></Route>
                <Route
                  path="/accessories/:id"
                  render={props => (
                    <AccessoriesDesc
                      data={this.state.data.accessories}
                      isAuth={this.state.isAuth}
                      updateCart={this.updateCart}
                      {...props}
                    ></AccessoriesDesc>
                  )}
                ></Route>
                <Route
                  path="/cart"
                  render={() => <Cart cartItem={this.state.cartItems} />}
                ></Route>
                <Route path="/career" component={Career}></Route>
                <Route path="/faq" component={Faq}></Route>
                <Route path="/aboutUs" component={About}></Route>
                <Route
                  path="/auth"
                  render={props => (
                    <Login
                      isAuth={this.state.isAuth}
                      auth={this.checkAuth}
                      {...props}
                    />
                  )}
                />
                <Route component={Notfound} />
              </Switch>
            ) : null}
          </div>
        </div>
        <Footer />
        </div>
      </Router>
    );
  }
}

export default App;
